<template>
  <div class="deposit-page-step-1">
    <DepositPageInfo />
    <DepositPageInput
      v-model="email"
      :placeholder="emailPlaceholder"
      @submit="processDeposit"
    />
  </div>
</template>

<script>
import { MODULES_NAMES } from '@/shared/common/modules'
import { useActions, useGetters, useMutations } from '@/shared/plugins/vuex'
import { computed, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import DepositPageInfo from '@/client/views/DepositPage/DepositPageInfo'
import DepositPageInput from '@/client/components/Inputs/DepositPageInput'

export default {
  name: 'Step1',
  components: {
    DepositPageInput,
    DepositPageInfo
  },
  emits: ['next'],
  setup (_, { emit }) {
    const { t, locale } = useI18n()
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)
    const {
      setEmail
    } = useActions(MODULES_NAMES.EXCHANGE)
    const {
      getEmail,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)

    const coinAmount = ref(20.62151445)
    const code = ref('MVK89JBNqZQv5zMAKG8FK3kJM4rjt')
    const emailPlaceholder = ref('notifyme@myemail.com')

    const email = computed({
      get: () => getEmail.value,
      set: setEmail
    })

    const processDeposit = () => {
      emit('next', 1)
    }

    setTools([
      {
        icon: {
          name: 'copy',
          width: '20px',
          height: '20px'
        },
        copyUrl: true
      }
    ])

    return {
      t,
      email,
      emailPlaceholder,
      locale,
      sendCoin,
      code,
      coinAmount,
      processDeposit
    }
  }
}
</script>

<style lang="scss">
.deposit-page-step-1 {

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    padding: 0;

    &__credentials {
      &__manually__inputs {
        margin-top: 40px;
        padding-left: 10px;
      }
      &__qrcode__body {
        padding-right: 10px;
      }
    }
  }

}
</style>
