<template>
  <Input
    v-model="value"
    v-bind="$attrs"
    class="deposit-page-input"
    :class="{
      'deposit-page-input__blured': !isFocused
    }"
    @focus="onInputFocus"
    @blur="onInputBlur"
  >
    <template #prepend>
      <Icon class="deposit-page-input__notify-bell" name="notify-bell" />
    </template>
    <template #append>
      <Button
        :style="{
          opacity: isFocused ? 1 : 0,
          cursor: isFocused ? 'pointer' : 'unset'
        }"
        :disabled="!isFocused"
        class="deposit-page-input__button"
        type="primary"
        @click="$emit('submit')"
      >
        <Icon name="arrow-right-full" />
      </Button>
    </template>
  </Input>
</template>

<script>
import { ref } from 'vue'
import { useModel } from '@/shared/utils/useModel'
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'

export default {
  name: 'DepositPageInput',
  inheritAttrs: true,
  components: { Icon, Button, Input },
  emits: ['submit', 'update:modelValue'],
  props: {
    modelValue: {
      type: String,
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)
    const isFocused = ref(false)

    const onInputFocus = () => {
      isFocused.value = true
    }
    const onInputBlur = () => {
      isFocused.value = false
    }
    return {
      value,
      isFocused,
      onInputBlur,
      onInputFocus
    }
  }
}
</script>

<style lang="scss">
.deposit-page-input {
  height: 56px;
  padding: 8px;

  .input {
    &__body__field {

      input {
        transition: all 200ms linear;
        color: #9A9999;
        padding: 0;
        margin: 0;
      }
    }
  }

  &__blured {
    .input {
      &__body__field {

        input {
          text-align: center;
          color: black;
          padding: 0;
          margin: 0;
        }
      }
    }
  }

  &__button {
    padding: 11px;
  }
  &__notify-bell {
    margin-left: 10px;
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    max-height: 56px;
  }
}
</style>
